/*** OpenLayer Set Up ***/

import 'ol/ol.css';
import Map from 'ol/Map';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import View from 'ol/View';
import TileLayer from 'ol/layer/Tile';
import {Fill, Stroke, Style} from 'ol/style';
import KML from 'ol/format/KML.js';
import Stamen from 'ol/source/Stamen.js';

/*** Map and Time Zone Set Up ***/

var styleFunction = function(feature) {
    var offset = 0;
    var name = feature.get('name'); // e.g. GMT -08:30
    var match = name.match(/([\-+]\d{2}):(\d{2})$/);
    if (match) {
        var hours = parseInt(match[1], 10);
        var minutes = parseInt(match[2], 10);
        offset = 60 * hours + minutes;
    }
    var date = new Date();
    var local = new Date(date.getTime() +
        (date.getTimezoneOffset() + offset) * 60000);
    // offset from local noon (in hours)
    var delta = Math.abs(12 - local.getHours() + (local.getMinutes() / 60));
    if (delta > 12) {
        delta = 24 - delta;
    }
    var opacity = 0.75 * (1 - delta / 12);
    return new Style({
        fill: new Fill({
            color: [0xff, 0xff, 0x33, opacity]
        }),
        stroke: new Stroke({
            color: '#ffffff'
        })
    });
};

const map = new Map({
    target: 'map-container',
    layers: [
        new TileLayer({
            source: new Stamen({
                layer: 'toner'
            })
        }),
        new VectorLayer({
            source: new VectorSource({
                url: 'http://openlayers.org/en/v3.18.2/examples/data/kml/timezones.kml',
                format: new KML({
                    extractStyles: false
                })
            }),
            style: styleFunction
        }),
    ],
    view: new View({
        center: [0, 0],
        zoom: 2
    })
});

var info = $('#info');
info.tooltip({
    animation: false,
    trigger: 'manual'
});

var displayFeatureInfo = function(pixel) {
    info.css({
        left: pixel[0] + 'px',
        top: (pixel[1] - 15) + 'px'
    });
    var feature = map.forEachFeatureAtPixel(pixel, function(feature) {
        return feature;
    });
    if (feature) {
        info.tooltip('hide')
            .attr('data-original-title', feature.get('name'))
            .tooltip('fixTitle')
            .tooltip('show');
    } else {
        info.tooltip('hide');
    }
};

map.on('pointermove', function(evt) {
    if (evt.dragging) {
        info.tooltip('hide');
        return;
    }
    displayFeatureInfo(map.getEventPixel(evt.originalEvent));
});

map.on('click', function(evt) {
    displayFeatureInfo(evt.pixel);
});

//------------------------------------------------------------------------------

/*** FlightPaths ***/

var style = new Style({
    stroke: new Stroke({
        color: '#ff0000',
        width: 2
    })
});

var flightsSource = new VectorSource({
    wrapX: false,
    attributions: 'Flight data by ' +
    '<a href="http://openflights.org/data.html">OpenFlights</a>,',
    loader: function() {
        var url = 'data/openflights/flights.json';
        fetch(url).then(function(response) {
            return response.json();
        }).then(function(json) {
            var flightsData = json.flights;
            for (var i = 0; i < flightsData.length; i++) {
                var flight = flightsData[i];
                var from = flight[0];
                var to = flight[1];

                // create an arc circle between the two locations
                var arcGenerator = new arc.GreatCircle(
                    {x: from[1], y: from[0]},
                    {x: to[1], y: to[0]});

                var arcLine = arcGenerator.Arc(100, {offset: 10});
                if (arcLine.geometries.length === 1) {
                    var line = new LineString(arcLine.geometries[0].coords);
                    line.transform('EPSG:4326', 'EPSG:3857');

                    var feature = new Feature({
                        geometry: line,
                        finished: false
                    });
                    // add the feature with a delay so that the animation
                    // for all features does not start at the same time
                    addLater(feature, i * 50);
                }
            }
            map.on('postcompose', animateFlights);
        });
    }
});

var flightsLayer = new VectorLayer({
    source: flightsSource,
    style: function(feature) {
        // if the animation is still active for a feature, do not
        // render the feature with the layer style
        if (feature.get('finished')) {
            return style;
        } else {
            return null;
        }
    }
});

map.addLayer(flightsLayer);

var pointsPerMs = 0.1;
function animateFlights(event) {
    var vectorContext = event.vectorContext;
    var frameState = event.frameState;
    vectorContext.setStyle(style);

    var features = flightsSource.getFeatures();
    for (var i = 0; i < features.length; i++) {
        var feature = features[i];
        if (!feature.get('finished')) {
            // only draw the lines for which the animation has not finished yet
            var coords = feature.getGeometry().getCoordinates();
            var elapsedTime = frameState.time - feature.get('start');
            var elapsedPoints = elapsedTime * pointsPerMs;

            if (elapsedPoints >= coords.length) {
                feature.set('finished', true);
            }

            var maxIndex = Math.min(elapsedPoints, coords.length);
            var currentLine = new LineString(coords.slice(0, maxIndex));

            // directly draw the line with the vector context
            vectorContext.drawGeometry(currentLine);
        }
    }
    // tell OpenLayers to continue the animation
    map.render();
}

function addLater(feature, timeout) {
    window.setTimeout(function() {
        feature.set('start', new Date().getTime());
        flightsSource.addFeature(feature);
    }, timeout);
}


